<!-- 监测设备信息侧边栏 -->
<template>
  <el-menu
    class="sidebar-el-menu"
    :collapse="collapse"
    background-color="#030e32"
    text-color="#fff"
    active-text-color="#20a0ff"
    @select="handleSelect"
  >
    <el-submenu index="1">
      <template slot="title">
        <i class="el-icon-location" style="color: #fff"></i>
        <span>设备信息</span>
      </template>
      <el-checkbox
        v-model="checkIconText"
        class="checkText"
        @change="changeTextIcon"
        >显示编号</el-checkbox
      >
    </el-submenu>
    <template v-for="item in items">
      <template v-if="item.subs">
        <el-submenu :index="item.index" :key="item.index">
          <template slot="title">
            <i :class="item.icon" style="color: #fff"></i>
            <span slot="title">{{ item.title }}</span>
          </template>
          <template v-for="subItem in item.subs">
            <el-submenu
              v-if="subItem.subs"
              :index="subItem.index"
              :key="subItem.index"
            >
              <template slot="title">{{ subItem.title }}</template>
              <el-menu-item
                v-for="(threeItem, i) in subItem.subs"
                :key="i"
                :index="threeItem.index"
                >{{ threeItem.title }}</el-menu-item
              >
            </el-submenu>
            <el-menu-item v-else :index="subItem.index" :key="subItem.item">{{
              subItem.title
            }}</el-menu-item>
          </template>
        </el-submenu>
      </template>
      <template v-else>
        <el-menu-item :index="item.index" :key="item.index">
          <i :class="item.icon" style="color: #fff"></i>
          <span slot="title">{{ item.title }}</span>
        </el-menu-item>
      </template>
    </template>
  </el-menu>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState } from "vuex";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      checkIconText: true,
      items: [
        {
          icon: "el-icon-data-line",

          index: "2",
          title: "滑坡",
          subs: [
            {
              index: "2-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "3",
          title: "崩塌",
          subs: [
            {
              index: "3-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "4",
          title: "泥石流",
          subs: [
            {
              index: "4-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "5",
          title: "地面塌陷",
          subs: [
            {
              index: "5-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "6",
          title: "地面沉降",
          subs: [
            {
              index: "6-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "7",
          title: "地裂缝",
          subs: [
            {
              index: "7-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "8",
          title: "活动断裂",
          subs: [
            {
              index: "8-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "9",
          title: "地下水环境",
          subs: [
            {
              index: "9-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "10",
          title: "土地质量",
          subs: [
            {
              index: "10-1",
              title: "数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "11",
          title: "城市垃圾",
          subs: [
            {
              index: "11-1",
              title: "数据管理",
            },
          ],
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {
    ...mapState("userAbout", ["collapse"]),
    ...mapState("envgeologyAbout", [
      "deviceList_slid",
      "deviceList_collaps",
      "deviceList_debriflow",
      "deviceList_groundcollaps",
      "deviceList_groundsub",
      "deviceList_groundcrack",
      "deviceList_activecrack",
      "deviceList_underwaterenv",
      "deviceList_groundquality",
      "deviceList_urbangarbage",
    ]), //生态地质监测模块中的监测设备
    deviceList() {
      return this.deviceList_slid.concat(
        this.deviceList_collaps,
        this.deviceList_debriflow,
        this.deviceList_groundcollaps,
        this.deviceList_groundsub,
        this.deviceList_groundcrack,
        this.deviceList_activecrack,
        this.deviceList_underwaterenv,
        this.deviceList_groundquality,
        this.deviceList_urbangarbage,
      );
    },
  },
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    handleSelect(key, keyPath) {
      switch (key) {
        case "2-1": //滑坡
          this.$router.push({
            path: "/envgeologyLandslide",
          });
          break;
        case "3-1": //崩塌
          this.$router.push({
            path: "/envgeologyCollaps",
          });
          break;
        case "4-1": //泥石流
          this.$router.push({
            path: "/envgeologyDebrisflow",
          });
          break;
        case "5-1": //地面塌陷
          this.$router.push({
            path: "/envgeologyGroundcollaps",
          });
          break;
        case "6-1": //地面沉降
          this.$router.push({
            path: "/envgeologyGroundsub",
          });
          break;
        case "7-1": //地裂缝
          this.$router.push({
            path: "/envgeologyGroundcrack",
          });
          break;
        case "8-1": //活动断裂
          this.$router.push({
            path: "/envgeologyActivecrack",
          });
          break;
        case "9-1": //地下水环境
          this.$router.push({
            path: "/envgeologyGroundwaterEnv",
          });
          break;
        case "10-1": //土地质量
          this.$router.push({
            path: "/envgeologyLandquality",
          });
          break;
        case "11-1": //城市垃圾
          this.$router.push({
            path: "/envgeologyUrbangarbage",
          });
          break;
      }
    },
    changeTextIcon(value) {
      this.$bus.$emit("changeFeatureText", value);
      //地图上点位名称-w
      this.$bus.$emit("addDeviceIconFeature", this.deviceList); //在地图中显示设备
    },
  },
};
</script>
<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  overflow-y: scroll;
  color: #030e32;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}

.checkText {
  padding-left: 40px;
}
.el-checkbox {
  color: #fff;
  font-size: 13px;
}
</style>
