import Sidebar2 from "../components/groundwater/Sidebar.vue";
import Sidebar3 from "../components/device/DevicePoint/Sidebar.vue";
import Sidebar4 from "../components/geologicalmap/Sidebar.vue";
import Sidebar5 from "../components/potential/Sidebar.vue";
import Sidebar6 from "../components/rainfall/Sidebar.vue";
import Sidebar7 from "../components/soilheavymetal/Sidebar.vue";
import Sidebar8 from "../components/submerge/Sidebar.vue";
import Sidebar9 from "../components/ecogeology/Sidebar.vue";
import Sidebar10 from "../components/geoenviroment/Sidebar.vue";
