//按需引入和加载
// Vue.component('sidebar1', function (resolve) {
//   import('../components/surfacewater/Sidebar.vue').then((module) => {
//     resolve(module.default);
//   });
// });
export default {
  components: {
    Header,
    CesiumMap,
    Map,
    mapState,
    Loading,
    Sidebar1,
    Sidebar2,
    Sidebar3,
    Sidebar4,
    Sidebar5,
    Sidebar6,
    Sidebar7,
    Sidebar8,
    Sidebar9,
    Sidebar10,
    Sidebar11,
  },
  data() {
    return {
      lastSegment: '',  //获取当前Url的最后一个/后面的内容
      isLoading: true,
      currentTitle: "监测点及监测设备管理",
      activeSubmenuIndex: 3, //控制切换submenu时候的标题文字的颜色
    };
  },
  created() {},
  computed: {
    ...mapState("userAbout", ["collapse"]),
  },
  mounted() {
    // 在组件挂载后获取当前 URL
    this.getLastSegment();
  },
  methods: {
    //
    getLastSegment() {
      const url = window.location.href;
      const segments = url.split('/');
      this.lastSegment = segments[segments.length - 1];
      // 使用 switch case 判断 lastSegment 的值
      switch (this.lastSegment) {
        case 'surfaceWater':
          this.currentTitle = '地表水';
          this.activeSubmenuIndex = 1;
          break;
        // 可根据需要添加其他情况的判断
        case 'groundWater':
          this.currentTitle = '地下水';
          this.activeSubmenuIndex = 2;
          break;
        case 'devicePoint':
          this.currentTitle = '监测点及监测设备管理';
          this.activeSubmenuIndex = 3;
          break;
        case 'geologicalMap':
          this.currentTitle = '二三维地质一张图';
          this.activeSubmenuIndex = 4;
          break;
        case 'potential':
          this.currentTitle = '潜势度及风险等级评估';
          this.activeSubmenuIndex = 5;
          break;
        case 'rainfall':
          this.currentTitle = '降雨量';
          this.activeSubmenuIndex = 6;
          break;
        case 'soilHeavyMetal':
          this.currentTitle = '土壤重金属';
          this.activeSubmenuIndex = 7;
          break;
        case 'submerge':
          this.currentTitle = '洪水淹没';
          this.activeSubmenuIndex = 8;
          break;
        case 'ecogeology':
          this.currentTitle = '生态地质监测';
          this.activeSubmenuIndex = 9;
          break;
        case 'envgeology':
          this.currentTitle = '地质环境监测';
          this.activeSubmenuIndex = 10;
          break;
        case 'soilcondition':
          this.currentTitle = '土壤信息';
          this.activeSubmenuIndex = 11;
          break;
        default:
          this.currentTitle = '监测点及监测设备管理';
          this.activeSubmenuIndex = 3;
          break;
      }
    },
    //也可以在data里面写数组 item写成v-for 函数写成switch case（index作参数）
    toGroundWater(index) {
      this.activeSubmenuIndex = index;
      this.$router.push("/groundWater");
      this.currentTitle = "地下水";
      console.log(this.currentTitle);
    },
    toDevicePoint(index) {
      this.activeSubmenuIndex = index;
      this.$router.push("/devicePoint");
      this.currentTitle = "监测点及监测设备管理";
      console.log(this.currentTitle);
    },
    toRainfall(index) {
      this.activeSubmenuIndex = index;
      this.$router.push("/rainfall");
      this.currentTitle = "降雨量";
      console.log(this.currentTitle);
    },
