<!-- 监测设备信息侧边栏 -->
<template>
  <el-menu
    class="sidebar-el-menu"
    :collapse="collapse"
    background-color="#030e32"
    text-color="#fff"
    active-text-color="#20a0ff"
    @select="handleSelect"
  >
    <el-submenu index="1">
      <template slot="title">
        <i class="el-icon-location" style="color: #fff"></i>
        <span>设备信息</span>
      </template>
      <el-checkbox
        v-model="checkIconText"
        class="checkText"
        @change="changeTextIcon"
        >显示编号</el-checkbox
      >
    </el-submenu>
    <template v-for="item in items">
      <template v-if="item.subs">
        <el-submenu :index="item.index" :key="item.index">
          <template slot="title">
            <i :class="item.icon" style="color: #fff"></i>
            <span slot="title">{{ item.title }}</span>
          </template>
          <template v-for="subItem in item.subs">
            <el-submenu
              v-if="subItem.subs"
              :index="subItem.index"
              :key="subItem.index"
            >
              <template slot="title">{{ subItem.title }}</template>
              <el-menu-item
                v-for="(threeItem, i) in subItem.subs"
                :key="i"
                :index="threeItem.index"
                >{{ threeItem.title }}</el-menu-item
              >
            </el-submenu>
            <el-menu-item v-else :index="subItem.index" :key="subItem.item">{{
              subItem.title
            }}</el-menu-item>
          </template>
        </el-submenu>
      </template>
      <template v-else>
        <el-menu-item :index="item.index" :key="item.index">
          <i :class="item.icon" style="color: #fff"></i>
          <span slot="title">{{ item.title }}</span>
        </el-menu-item>
      </template>
    </template>
  </el-menu>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState } from "vuex";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      checkIconText: true,
      items: [
        {
          icon: "el-icon-data-line",

          index: "2",
          title: "地貌形态",
          subs: [
            {
              index: "2-1",
              title: "数据管理",
            },
            {
              index: "2-2",
              title: "统计查询",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "3",
          title: "土地利用",
          subs: [
            {
              index: "3-1",
              title: "数据管理",
            },
            {
              index: "3-2",
              title: "统计查询",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "4",
          title: "植被覆盖",
          subs: [
            {
              index: "4-1",
              title: "数据管理",
            },
            {
              index: "4-2",
              title: "统计查询",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "5",
          title: "湿地变化",
          subs: [
            {
              index: "5-1",
              title: "数据管理",
            },
            {
              index: "5-2",
              title: "统计查询",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "6",
          title: "水体分布",
          subs: [
            {
              index: "6-1",
              title: "数据管理",
            },
            {
              index: "6-2",
              title: "统计查询",
            },
          ],
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {
    ...mapState("userAbout", ["collapse"]),
    ...mapState("ecogeologyAbout", [
      "deviceList_lform",
      "deviceList_luse",
      "deviceList_tcover",
      "deviceList_wdistribute",
      "deviceList_wetchange",
    ]), //生态地质监测模块中的监测设备
    deviceList() {
      return this.deviceList_lform.concat(
        this.deviceList_luse,
        this.deviceList_tcover,
        this.deviceList_wdistribute,
        this.deviceList_wetchange
      );
    },
  },
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    handleSelect(key, keyPath) {
      switch (key) {
        case "2-1": //地貌形态数据管理
          this.$router.push({
            path: "/ecogeologyLandForm",
          });
          break;
        case "2-2": //地貌形态统计查询
          this.$router.push({
            path: "/averageLandForm",
          });
          break;
        case "3-1": //土地利用数据管理
          this.$router.push({
            path: "/ecogeologyLandUse",
          });
          break;
        case "3-2": //土地利用统计查询
          this.$router.push({
            path: "/averageLandUse",
          });
          break;
        case "4-1": //植被覆盖数据管理
          this.$router.push({
            path: "/ecogeologyVegetationCover",
          });
          break;
        case "4-2": //植被覆盖统计查询
          this.$router.push({
            path: "/averageVegetationCover",
          });
          break;
        case "5-1": //水体分布数据管理
          this.$router.push({
            path: "/ecogeologyWaterDistribute",
          });
          break;
        case "5-2": //水体分布统计查询
          this.$router.push({
            path: "/averageWaterDistribute",
          });
          break;
        case "6-1": //湿地变化数据管理
          this.$router.push({
            path: "/ecogeologyWetlandChange",
          });
          break;
        case "6-2": //湿地变化统计查询
          this.$router.push({
            path: "/averageWetlandChange",
          });
          break;
      }
    },
    changeTextIcon(value) {
      this.$bus.$emit("changeFeatureText", value);
      //地图上点位名称-w
      this.$bus.$emit("addDeviceIconFeature", this.deviceList); //在地图中显示设备
    },
  },
};
</script>
<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  overflow-y: scroll;
  color: #030e32;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}

.checkText {
  padding-left: 40px;
}
.el-checkbox {
  color: #fff;
  font-size: 13px;
}
</style>
