<!-- 监测设备信息侧边栏 -->
<template>
  <el-menu
    class="sidebar-el-menu"
    :collapse="collapse"
    background-color="#030e32"
    text-color="#fff"
    active-text-color="#20a0ff"
    @select="handleSelect"
  >
    <el-submenu index="1">
      <template slot="title">
        <i class="el-icon-location" style="color: #fff"></i>
        <span>设备信息</span>
      </template>
      <el-checkbox
        v-model="checkIconText"
        class="checkText"
        @change="changeTextIcon"
        >显示编号</el-checkbox
      >
    </el-submenu>
    <template v-for="item in items">
      <template v-if="item.subs">
        <el-submenu :index="item.index" :key="item.index">
          <template slot="title">
            <i :class="item.icon" style="color: #fff"></i>
            <span slot="title">{{ item.title }}</span>
          </template>
          <template v-for="subItem in item.subs">
            <el-submenu
              v-if="subItem.subs"
              :index="subItem.index"
              :key="subItem.index"
            >
              <template slot="title">{{ subItem.title }}</template>
              <el-menu-item
                v-for="(threeItem, i) in subItem.subs"
                :key="i"
                :index="threeItem.index"
                >{{ threeItem.title }}</el-menu-item
              >
            </el-submenu>
            <el-menu-item v-else :index="subItem.index" :key="subItem.item">{{
              subItem.title
            }}</el-menu-item>
          </template>
        </el-submenu>
      </template>
      <template v-else>
        <el-menu-item :index="item.index" :key="item.index">
          <i :class="item.icon" style="color: #fff"></i>
          <span slot="title">{{ item.title }}</span>
        </el-menu-item>
      </template>
    </template>
  </el-menu>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState } from "vuex";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      checkIconText: true,
      items: [
        {
          icon: "el-icon-share",
          index: "2",
          title: "土壤化验",
          subs: [
            {
              index: "2-1",
              title: "化验数据管理",
            },
          ],
        },
        {
          icon: "el-icon-data-line",
          index: "3",
          title: "土壤采样",
          subs: [
            {
              index: "3-1",
              title: "采样数据管理",
            },
          ],
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {
    ...mapState("userAbout", ["collapse"]),
    ...mapState("soilConditionAbout", [
      "deviceList_test",
      "deviceList_samp",
    ]), //土壤模块中的监测设备
    deviceList() {
      return this.deviceList_samp.concat(this.deviceList_test);
    },
  },
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    handleSelect(key, keyPath) {
      switch (key) {
        case "2-1": 
          this.$router.push({
            path: "/soiltestTable",
          });
          break;

        case "3-1": 
          this.$router.push({
            path: "/soilsampTable",
          });
          break;

      }
    },
    changeTextIcon(value) {
      this.$bus.$emit("changeFeatureText", value);
      this.$bus.$emit("addDeviceIconFeatureFor3D", this.deviceList); //在地图中显示设备
    },
  },
};
</script>
<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  overflow-y: scroll;
  color: #030e32;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}

.checkText {
  padding-left: 40px;
}
.el-checkbox {
  color: #fff;
  font-size: 13px;
}
</style>
